<template>
  <div class="home">
    <van-nav-bar :fixed="true" :border="false" >
      <template #left>
        <div class="home-title">
         <span v-if="isLogin">{{timeText}}, {{userInfo.nickName}}</span>
          <span v-else @click="goLogin">请先去登录</span>
        </div>
      </template>
      <!-- <template #right>
        <div class="location" >
          <van-icon name="location" color="#0C34BA" size="20px" />
          <p> 广州天河区</p>
          <van-icon name="arrow" />
          <div class="distance ">520m</div>
        </div>
      </template> -->
      <!-- <template #right>
        <van-search placeholder="请输入商品名称" shape="round" />
      </template>-->
    </van-nav-bar>
    <div class="banner-box">
      <van-swipe :autoplay="5000" :show-indicators="false" height="200">
        <van-swipe-item
          v-for="(item, index) in bannerData"
          :key="index"
          @click="viewProductInfo(item.pid)"
        >
          <img class="auto-img" :src="item.bannerImg" alt />
          <van-tag round color="white" text-color="#000" size="large">{{item.name}}</van-tag>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="hot-product">
      <div class="hot-product-title">
        人气
        <span>Top</span>
        <img src="../../assets/images/hot.svg" alt />
      </div>
      <div class="product-box clearfix">
        <div
          class="product-item fl"
          :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
          v-for="(item, index) in hotProducts"
          :key="index"
        >
          <div class="p-img" @click="viewProductInfo(item.pid)">
            <img class="auto-img" :src="item.smallImg" alt />
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="clearfix">
            <!-- <div class="en-pro-name one-text fl">{{item.enname}}</div> -->
            <div class="pro-price fl">￥{{item.price}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/home.less";

export default {
  name: "Home",
  data() {
    return {
      timeText: "",
      bannerData: [],
      hotProducts: [],
      userInfo: {},
      isLogin:false
    };
  },

  created() {
    // 初始化数据
    // 时间
    this.getTime();
    // 用户信息
    this.getUserInfo();
    // 轮播图data
    this.getbannerData();
    // 热门商品
    this.getHotProducts();
  },

  methods: {
    goLogin(){
      this.$router.push({name:'Login'})
    },
    //获取时间段
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好";
      } else {
        this.timeText = "深夜好";
      }
    },
    //查询信息
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "get",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    //获取banner
    getbannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    //获取推荐商品数据
    getHotProducts() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    //查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
  },
};
</script>